.heading {
  font-family: $accent-font-family;
  font-size: 24px;
  line-height: 31px;
  font-weight: 400;

  @media #{$laptop-desktop} {
    font-size: 36px;
    line-height: 47px;
  }
}

.heading--small {
  @media #{$laptop-desktop} {
    font-size: 24px;
    line-height: 31px;
  }
}

