.popup {
  z-index: 1000000;
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  padding: 18px 10px 25px;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.2);
  overflow: auto;
}
.popup--open {
  display: grid;
}

