.note__accent {
  display: grid;
  margin: 0 0 1px;
  justify-content: center;
  column-gap: 10px;
  grid-auto-flow: column;
  font-size: 12px;
  line-height: 13px;
  font-weight: 500;
  letter-spacing: -0.01em;
}
@media (min-width: 375px) {
