    margin-bottom: 7px;
  }
}
@media (min-width: 1280px) {
  .note__accent {
    margin-bottom: 2px;
  }
}

.note__accent::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-color: #42bd98;
  mask-image: url("../img/icons/note-icon.svg");
  mask-repeat: no-repeat;
  mask-position: center;
}
@media (min-width: 375px) {
  .note__accent::before {
    position: absolute;
    top: 5px;
    left: 5px;
  }
}
@media (min-width: 768px) {
  .note__accent::before {
    top: 8px;
  }
}
@media (min-width: 1280px) {
  .note__accent::before {
    top: 5px;
  }
}

.note__accent strong,
.note__accent small {
  font: inherit;
}

.note__accent strong {
  margin: 0;
}

.note__text {
