  background-repeat: space no-repeat;
}

.evaluation::after {
  content: "";
  grid-area: icon;
  width: 0;
  height: 19px;
  background-image: url("../img/icons/evaluation-star-active.svg");
  background-repeat: space no-repeat;
}

